import { cache } from 'common/js/cache.ts';
import { initPlayData } from 'common/js/initData';
var defaultLogin = {
    isLogin: false,
    userData: {}
};
// 从缓存中获取并合并之前保存的播放数据，但播放状态和全屏为默认值
var playData = Object.assign(cache.get(initPlayData, 'playData'), { playing: false, fullScreen: false });
export default {
    userLoginState: cache.get(defaultLogin, 'LOGIN'),
    viewport: { width: document.body.clientWidth, height: window.innerHeight },
    playData: playData,
    searchHistory: cache.get([], 'searchHistory'),
    playHistory: cache.get([], 'playHistory')
};



