import request from './request';
var addCollectToPlayList = function (sid, pid) {
    var target = '/playlist/tracks';
    var query = {
        op: 'add',
        pid: pid,
        tracks: sid
    };
    return request({ target: target, query: query }, true);
};
var getLyric = function (sid) {
    var target = '/lyric';
    var query = {
        id: sid
    };
    return request({ target: target, query: query }, false);
};
var getSimiSong = function (sid) {
    var target = '/simi/song';
    var query = {
        id: sid
    };
    return request({ target: target, query: query }, false);
};
var getSongUrl = function (sid) {
    var target = '/music/url';
    var query = {
        id: sid
    };
    return request({ target: target, query: query }, false);
};
var getSongComment = function (sid, limit, offset) {
    var target = '/comment/music';
    var query = {
        id: sid,
        limit: limit
    };
    if (offset)
        query.offset = offset;
    return request({ target: target, query: query }, false);
};
var getDjComment = function (sid, limit, offset) {
    var target = '/comment/dj';
    var query = {
        id: sid,
        limit: limit
    };
    if (offset)
        query.offset = offset;
    return request({ target: target, query: query }, false);
};
var getRecFm = function () {
    var target = '/personal_fm';
    var query = {
        timestamp: Date.now()
    };
    return request({ target: target, query: query }, true);
};
var like = function (id, liked) {
    var target = '/like';
    var query = {
        id: id,
        like: liked
    };
    return request({ target: target, query: query }, true);
};
var fmTrash = function (id) {
    var target = '/fm_trash';
    var query = {
        id: id
    };
    return request({ target: target, query: query }, true);
};
export { addCollectToPlayList, fmTrash, like, getLyric, getSimiSong, getSongComment, getSongUrl, getDjComment, getRecFm };



