import request from './request';
var getAdviceData = function (keywords) {
    var target = '/search/suggest';
    var query = {
        keywords: keywords
    };
    return request({ target: target, query: query }, false);
};
var getSearchResultData = function (keywords, type) {
    var target = '/search';
    var query = {
        keywords: keywords,
        type: type
    };
    return request({ target: target, query: query }, false);
};
export { getAdviceData, getSearchResultData };



