import lscache from 'lscache';
var LC_KEY = '__netease__';
var API_KEY = LC_KEY + 'API';
var SIXHOUR = 6 * 60 * 60 * 1000;
import request from './request';
var cacheRequest = function (data, needCooike) {
    if (needCooike === void 0) { needCooike = false; }
    // 尝试从本地获取，并检验时间戳
    var target = data.target;
    var keys = Object.keys(data.target);
    var tryCache = lscache.get(target + +API_KEY);
    return new Promise(function (resolve, reject) {
        if (tryCache && Date.now() - tryCache.timetamp < SIXHOUR) {
            resolve(tryCache);
        }
        else {
            request(data, needCooike).then(function (res) {
                res.timetamp = Date.now();
                lscache.set(target + API_KEY, res);
                resolve(res);
            }, function (err) {
                reject(err);
            });
        }
    });
};
export default cacheRequest;



