import request from './request.ts';
function userLogin(loginMessage) {
    var target = '/login/cellphone';
    var query = loginMessage;
    return request({ target: target, query: query }, true);
}
var zhijie = function () {
    var target = '/login/cellphone';
    var query = { phone: '13221338819', password: 'nokia720' };
    return request({ target: target, query: query }, true);
};
var signIn = function () {
    var target = '/daily_signin';
    var query = {
        timestamp: Date.now()
    };
    return request({ target: target, query: query }, true);
};
export { zhijie, userLogin, signIn };



