import lscache from 'lscache';
var LC_KEY = '__netease__';
var LOGIN_KEY = LC_KEY + 'LOGIN';
export var cache = {
    set: function (val, key) {
        setTimeout(function () {
            lscache.set(key + LC_KEY, val);
        }, 5000);
        return val;
    },
    get: function (def, key) {
        var r = lscache.get(key + LC_KEY);
        if (r)
            return r;
        return def;
    }
};



