import request from './request';
var getHighqualityPlaylist = function (tag, limit) {
    var target = '/top/playlist/highquality';
    var query = {
        cat: tag ? tag : '',
        limit: limit ? limit : 50
    };
    return request({ target: target, query: query }, false);
};
var getSonglistDetail = function (id) {
    var target = '/playlist/detail';
    var query = { id: id };
    return request({ target: target, query: query }, false);
};
export { getHighqualityPlaylist, getSonglistDetail };



