import superagent from 'superagent';
import { SERVER_URI } from './config.ts';
var request = function request(data, needCooike) {
    var url = SERVER_URI + data.target;
    var query = data.query ? data.query : {};
    if (needCooike) {
        return new Promise(function (resolve, reject) {
            superagent
                .get(url)
                .withCredentials()
                .query(query)
                .end(function (err, res) {
                if (err) {
                    reject(err);
                }
                else {
                    resolve(res);
                }
            });
        });
    }
    else {
        return new Promise(function (resolve, reject) {
            superagent
                .get(url)
                .query(query)
                .end(function (err, res) {
                if (err) {
                    reject(err);
                }
                else {
                    resolve(res);
                }
            });
        });
    }
};
export default request;



