import request from './request';
var getSongListComment = function (id) {
    var target = '/comment/playlist';
    var query = {
        id: id
    };
    return request({ target: target, query: query }, false);
};
var getAlbumComment = function (id) {
    var target = '/comment/album';
    var query = {
        id: id
    };
    return request({ target: target, query: query }, false);
};
export { getSongListComment, getAlbumComment };



