import request from './request';
import cacheRequest from './api-cache';
// 获取分类，几乎没有更新要求，使用缓存机制
var getDjCatelist = function () {
    var target = '/dj/catelist';
    return cacheRequest({ target: target }, false);
};
var getDjRecommend = function () {
    var target = '/dj/recommend';
    return request({ target: target }, false);
};
var getCateRecommend = function (type) {
    var target = '/dj/recommend/type';
    var query = {
        type: type
    };
    return request({ target: target, query: query }, false);
};
var getDjDetail = function (rid) {
    var target = '/dj/detail';
    var query = { rid: rid };
    return request({ target: target, query: query }, false);
};
var getDjPrograms = function (rid) {
    var target = '/dj/program';
    var query = { rid: rid };
    return request({ target: target, query: query }, false);
};
export { getDjCatelist, getDjRecommend, getCateRecommend, getDjDetail, getDjPrograms };



